import React from 'react';
import { ChatBubbleLeftRightIcon, CodeBracketIcon, UserCircleIcon, ShieldCheckIcon, DevicePhoneMobileIcon, BoltIcon } from '@heroicons/react/20/solid';
import { ClockIcon, GlobeAltIcon } from '@heroicons/react/24/outline';

const features = [
  {
    name: '  ',
    description: ' WebSocket  Socket.IO     .',
    icon: ChatBubbleLeftRightIcon,
  },
  {
    name: ' Markdown',
    description: '  Markdown    .',
    icon: CodeBracketIcon,
  },
  {
    name: ' ',
    description: 'JWT-       .',
    icon: ShieldCheckIcon,
  },
  {
    name: '  AI',
    description: '       .',
    icon: BoltIcon,
  },
  {
    name: ' ',
    description: '      Tailwind CSS.',
    icon: DevicePhoneMobileIcon,
  },
  {
    name: '   ',
    description: '       .',
    icon: UserCircleIcon,
  },
  {
    name: ' ',
    description: '            .',
    icon: ClockIcon,
  },
  {
    name: ' ', //  
    description: '       ,        .',
    icon: GlobeAltIcon, //     , ,  
  },
];


export default function Feature() {
  return (
    <div className="overflow-hidden bg-white py-24 sm:py-32">
      <div className="mx-auto max-w-7xl px-6 lg:px-8">
        <div className="mx-auto grid max-w-2xl grid-cols-1 gap-x-8 gap-y-16 sm:gap-y-20 lg:mx-0 lg:max-w-none lg:grid-cols-2">
          <div className="lg:pt-4 lg:pr-8">
            <div className="lg:max-w-lg">
              <h2 className="text-base/7 font-semibold text-indigo-600"> Hunt AI</h2>
              <p className="mt-2 text-4xl font-semibold tracking-tight text-pretty text-gray-900 sm:text-5xl">
                   
              </p>
              <p className="mt-6 text-lg/8 text-gray-600">
                ,       AI,    .
              </p>
              <dl className="mt-10 max-w-xl space-y-8 text-base/7 text-gray-600 lg:max-w-none">
                {features.map((feature) => (
                  <div key={feature.name} className="relative pl-9">
                    <dt className="inline font-semibold text-gray-900">
                      <feature.icon aria-hidden="true" className="absolute top-1 left-1 size-5 text-indigo-600" />
                      {feature.name}
                    </dt>{' '}
                    <dd className="inline">{feature.description}</dd>
                  </div>
                ))}
              </dl>
            </div>
          </div>
          <img
            alt=" "
            src="/screen1.jpeg"
            width={2432}
            height={1442}
            className="w-[48rem] max-w-none rounded-xl ring-1 shadow-xl ring-gray-400/10 sm:w-[57rem] md:-ml-4 lg:-ml-0"
          />
        </div>
      </div>
    </div>
  );
}
